import React, { Component } from 'react';
import Card from './Card.js';


class App extends Component { //можно писать Component вместо React.Component так как вверху мы сделали импорт Component.
  constructor() {
    super();
    this.state = {
      login: ""
    }
  }

  handleChange = (event) => {
    const {name, value} = event.target
      this.setState({
        [name]: value
      });
  }

  render() {
    return (
      <div className="tc">
        <textarea 
          placeholder='укажите Ваш логин'
          onChange={this.handleChange}
          name='login'
        ></textarea>
        <Card 
          input={this.state.login}
        />
        <h2>{this.state.login}</h2>
      </div>
    );
  }
}

